import React, { Component } from 'react'
import { css } from '@emotion/core'
import '../grid.css'
import '../style.css'
import 'antd/dist/antd.css'
import PropTypes from 'prop-types'
import { Layout, Button, Card, Form, Icon, Input, Checkbox, Tooltip } from 'antd'
import { Link } from 'Config/routes'
import Router from 'next/router'

class Login extends Component {

  constructor(props) {
    super(props)
  }

  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if(!err) {
        Router.push('/dashboard')
      }
    })
  }

  render(){
    const { getFieldDecorator } = this.props.form
    const loginCard = css`
    background: #fff;
    width: 350px;
    border-radius: 8px;
    border-top: 4px solid rgb(24, 144, 255);
    box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
    @media(max-width: 700px) {
      width: 100%;
    }
  `
    return(
      <Layout>
        <div className="container" css={css`
          display: flex;
          position: relative;
          height: 100vh;
          justify-content: center;
          align-items: center;
        `}>
          <Card bordered={false} css={ loginCard }>
            <div>
              <div css={css`img { width: 150px;} display: flex; justify-content: center;`}>
                <img src="/static/icons/userlogin.svg" alt=""/>
              </div>
              <Form className="login-form" onSubmit={ this.handleSubmit }>
                <Form.Item>
                  { getFieldDecorator('userName', {
                    rules: [{ required: true, message: 'Masukkan Username Anda!'}],
                  })(
                    <Input 
                      prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} 
                      suffix={
                        <Tooltip title="Masukkan Username Anda">
                          <Icon type="info-circle" style={{ color: 'rgba(0,0,0,.45)' }} />
                        </Tooltip>
                      }
                      placeholder="Username" 
                    />
                  )}
                </Form.Item>
                <Form.Item>
                  {getFieldDecorator('password', {
                    rules: [{ required: true, message: 'Please input your Password!' }],
                  })(
                    <Input.Password prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
                  )}
                </Form.Item>
                <Form.Item>
                  <div css={css`
                    display: flex;
                    justify-content: space-between;
                  `}>
                    {getFieldDecorator('remember', {
                      valuePropName: 'checked',
                      initialValue: true,
                    })(
                      <Checkbox>Remember me</Checkbox>
                    )}
                    <Link route="resetPassword">
                      <a className="login-form-forgot">Forgot password</a>
                    </Link>
                  </div>
                  <Button type="primary" htmlType="submit" className="login-form-button" block>
                  Log in
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </Card>
        </div>
      </Layout>
    )
  }
}

export default Form.create({ name: "loginForm"})(Login)